  //   ],
  // });
  // slogan.style.filter = 'url(#frosted)';

  // const bgLosung = createElement({
  //   tag: 'div',
  //   attributes: { id: 'bg-losung' },
  //   classes: [
  //     'font-nexa-bold',
  //     'px-4',
  //     'pb-16',
  //     'bg-transparent',
  //     // 'rounded-md',
  //     'text-center',
  //     'max-w-md',
  //     'z-30',
  //   ],
  //   parent: slogan,
  // });

  // // Create and animate lines for bgLosung
  // const losungTextLines = [
  //   'Life is water.',
  //   "So let's make",
  //   'this life happy!',
  // ];
  // const animatedLineElements: HTMLElement[] = [];

  // for (const text of losungTextLines) {
  //   const lineContainer = createElement({
  //     tag: 'div',
  //     classes: ['overflow-hidden', 'relative', 'leading-8'],
  //     parent: bgLosung,
  //   });

  //   const textElement = createElement({
  //     tag: 'span',
  //     text,
  //     classes: ['inline-block', 'whitespace-nowrap'],
  //     // classes: ['text-white', 'inline-block', 'whitespace-nowrap'],
  //     parent: lineContainer,
  //     attributes: {
  //       'data-text': text,
  //     },
  //   });
  //   animatedLineElements.push(textElement);
  // }

  // if (animatedLineElements.length > 0) {
  //   gsap.fromTo(
  //     animatedLineElements,
  //     { yPercent: 150 },
  //     {
  //       yPercent: 25,
  //       stagger: 0.25,
  //       duration: 0.8,
  //       ease: 'power2.out',
  //       delay: 0.3,
  //     }
  //   );
  // }
  // slogan.style.top = '10%';

// slogan.style.top = '50%';
// slogan.style.opacity = '1';

  const bubbles: Bubble[] = [];
  const maxBubbles = 15;

  function addBubble(): void {
    if (bubbles.length < maxBubbles) {
      bubbles.push(new Bubble());
    }
  }

  function startAddingBubbles(): void {
    const interval = Math.random() * 500 + 300;
    addBubble();
    setTimeout(startAddingBubbles, interval);
  }

  function animate(): void {
    if (!ctx) return;
    ctx.clearRect(0, 0, canvas.width, canvas.height);

    bubbles.forEach((bubble) => {
      bubble.update();
      bubble.draw();
    });

    requestAnimationFrame(animate);
  }

  // GSAP scroll-based directional movement
  let lastScrollY = window.scrollY;
  window.addEventListener('scroll', () => {
    const currentScrollY = window.scrollY;
    const direction = currentScrollY > lastScrollY ? 'down' : 'up';
    lastScrollY = currentScrollY;

    const offset = direction === 'down' ? -15 : 15;

    bubbles.forEach((bubble) => {
      gsap.to(bubble, {
        dx: bubble.dx + offset,
        dy: bubble.dy + offset,
        duration: 1,
        ease: 'power2.out',
      });
    });
  });

  startAddingBubbles();
  animate();

  window.addEventListener('resize', () => {
    canvas.width = window.innerWidth;
    canvas.height = window.innerHeight;
    bubbles.forEach((bubble) => bubble.reset());
  });

  setupBackgroundAnimations();

  // gsap.to(slogan, {
  //   top: '0%',
  //   ease: 'none',
